"use client"

import { useState, useEffect } from "react"
import {
  LayoutDashboard,
  Menu,
  BookOpen,
  MessageSquare
} from "lucide-react"

import { Button } from "@/components/ui/button"
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet"
import { InstructorSidebar } from "./instructor/instructor-sidebar"
import { OverviewAnalytics } from "./instructor/overview-analytics"
import { CourseManager } from "./instructor/course-manager"
import { CommunicationCenter } from "./instructor/communication-center"
import { getInstructorStats, getCourses } from "@/lib/actions"
import { Loader2 } from "lucide-react"

import { CourseEditor } from "./instructor/course-editor/course-editor"

export function CourseAdminDashboard() {
  const [activeTab, setActiveTab] = useState("dashboard")
  const [stats, setStats] = useState<any>(null)
  const [courses, setCourses] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [editingCourse, setEditingCourse] = useState<any>(null)

  const loadData = async () => {
    try {
      const [statsData, coursesData] = await Promise.all([
        getInstructorStats(),
        getCourses()
      ])
      setStats(statsData)
      setCourses(coursesData)
    } catch (error) {
      console.error("Failed to load instructor data", error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    loadData()
  }, [])

  // If editing a course, show the editor full screen (replacing dashboard)
  if (editingCourse) {
    return (
      <CourseEditor
        course={editingCourse}
        onBack={() => setEditingCourse(null)}
        onSave={(updated) => {
          // Update local list for immediate feedback
          setCourses(courses.map(c => c.id === updated.id ? updated : c))
        }}
      />
    )
  }

  const renderContent = () => {
    if (loading) {
      return <div className="flex justify-center items-center h-96"><Loader2 className="animate-spin text-primary" /></div>
    }

    switch (activeTab) {
      case "dashboard":
        return <OverviewAnalytics stats={stats} />
      case "courses":
        return <CourseManager
          courses={courses}
          onCourseCreated={loadData}
          onEditCourse={setEditingCourse}
        />
      case "communication":
        return <CommunicationCenter />
      default:
        return <div className="text-center py-20 text-muted-foreground">Component for {activeTab} under construction</div>
    }
  }

  return (
    <div className="flex min-h-screen bg-background rounded-lg border overflow-hidden">
      {/* Desktop Sidebar */}
      <div className="hidden md:block w-64 shrink-0">
        <div className="h-full border-r bg-card py-4">
          <div className="px-3 py-2">
            <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
              Instructor
            </h2>
            <div className="space-y-1">
              <Button
                variant={activeTab === "dashboard" ? "secondary" : "ghost"}
                className="w-full justify-start"
                onClick={() => setActiveTab("dashboard")}
              >
                <LayoutDashboard className="mr-2 h-4 w-4" />
                Dashboard
              </Button>
              <Button
                variant={activeTab === "courses" ? "secondary" : "ghost"}
                className="w-full justify-start"
                onClick={() => setActiveTab("courses")}
              >
                <BookOpen className="mr-2 h-4 w-4" />
                Courses
              </Button>
              <Button
                variant={activeTab === "communication" ? "secondary" : "ghost"}
                className="w-full justify-start"
                onClick={() => setActiveTab("communication")}
              >
                <MessageSquare className="mr-2 h-4 w-4" />
                Communication
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Header & Content */}
      <div className="flex-1 flex flex-col min-w-0">
        <header className="md:hidden flex items-center border-b p-4 bg-card">
          <Sheet>
            <SheetTrigger asChild>
              <Button variant="ghost" size="icon"><Menu /></Button>
            </SheetTrigger>
            <SheetContent side="left" className="p-0 w-64">
              <div className="h-full py-4">
                <div className="px-3 py-2">
                  <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
                    Instructor
                  </h2>
                  <div className="space-y-1">
                    <Button
                      variant={activeTab === "dashboard" ? "secondary" : "ghost"}
                      className="w-full justify-start"
                      onClick={() => setActiveTab("dashboard")}
                    >
                      <LayoutDashboard className="mr-2 h-4 w-4" />
                      Dashboard
                    </Button>
                    <Button
                      variant={activeTab === "courses" ? "secondary" : "ghost"}
                      className="w-full justify-start"
                      onClick={() => setActiveTab("courses")}
                    >
                      <BookOpen className="mr-2 h-4 w-4" />
                      Courses
                    </Button>
                    <Button
                      variant={activeTab === "communication" ? "secondary" : "ghost"}
                      className="w-full justify-start"
                      onClick={() => setActiveTab("communication")}
                    >
                      <MessageSquare className="mr-2 h-4 w-4" />
                      Communication
                    </Button>
                  </div>
                </div>
              </div>
            </SheetContent>
          </Sheet>
          <h1 className="ml-4 font-bold">Instructor Dashboard</h1>
        </header>

        <main className="flex-1 p-6 overflow-y-auto">
          {renderContent()}
        </main>
      </div>
    </div>
  )
}
